<template>
  <div>
    <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
      <el-row>
        <el-col :span="22">
          <el-form-item label="子系统代码" prop="sysCode">
            <el-input type="text" v-model="dataForm.sysCode" maxlength='15' placeholder="请输入区域代码"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="子系统名称" prop="sysName">
            <el-input type="text" v-model="dataForm.sysName" maxlength='10' placeholder="请输入区域名称"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="子系统状态" prop="sysStsatus">
            <el-radio-group v-model="dataForm.sysStsatus">
              <el-radio :label="0">停用</el-radio>
              <el-radio :label="1">启用</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="子系统描述" prop="sysDesc">
            <el-input type="textarea" :rows="5" v-model="dataForm.sysDesc"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <div class="dialog-footer">
        <el-button type="primary" @click="saveArea()">保存</el-button>
        <el-button type="danger" @click="cancle()">取消</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'add-subsystem-dialog',
  props: [
    'checkedData',
    'isEdit'
  ],
  data () {
    return {
      //表单验证
      rules: {
        sysCode: [{
          required: true,
          message: '请输入子系统代码',
          trigger: 'change'
        }],
        sysName: [{
          required: true,
          message: '请输入子系统名称',
          trigger: 'change'
        }],
      },
      dataForm: {
        sysCode: "",
        sysName: "",
        sysStsatus: 0,
        sysDesc: "",
        sysId: "",//子系统ID
        sysIcon: "",//子系统图标
        sysPath: "",//子系统路径

      },
    }
  },
  created () {
    this.setFormData();
  },
  methods: {
    setFormData () {
      if (this.isEdit === false) {
        this.dataForm.sysCode = this.checkedData.sysCode
        this.dataForm.sysName = this.checkedData.sysName
        this.dataForm.sysStsatus = Number(this.checkedData.sysStsatus)
        this.dataForm.sysDesc = this.checkedData.sysDesc
        this.dataForm.sysId = this.checkedData.sysId
      }
    },
    //保存
    saveArea () {
      this.$refs['addForm'].validate((valid) => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl(this.API.savaOrUpdateSystem),
            method: 'post',
            data: this.$http.adornData(
              this.dataForm
            )
          }).then((res) => {
            var resData = res.data;
            if (resData.code == 0) {
              this.$message({
                message: '成功',
                type: 'success'
              });
              this.$emit("addback", true);
            } else {
              this.$message.error(resData.msg);
            }
          });
        }
      })
    },
    //取消
    cancle () {
      this.$emit("addback", false);
    }
  }
}
</script>

<style>
</style>



